import React from "react";
import { navigate } from "gatsby";

import { createBody } from "utils/contentful";
import { find, pipe, filter, map, propEq } from "ramda";
import Layout from "components/layout";
import pin from "assets/img/icons/black/light-fill/map-pin.svg";
import globe from "assets/img/icons/black/light-fill/globe.svg";
import team from "assets/img/icons/black/light-fill/team.svg";
import qb from "assets/img/logos/not-brex/color/quickbooks.png";
import netsuite from "assets/img/logos/not-brex/color/oracle-netsuite.png";
import expensify from "assets/img/logos/not-brex/color/expensify.png";
import xero from "assets/img/logos/not-brex/color/xero.png";
import concur from "assets/img/logos/not-brex/color/concur.png";
import fb from "assets/img/icons/logos/fb.svg";
import linkedin from "assets/img/icons/logos/linkedin.svg";
import twitter from "assets/img/icons/logos/twitter.svg";
import elite from "assets/img/partners/accounting/elite.svg";
import premium from "assets/img/partners/accounting/premium.svg";
import starter from "assets/img/partners/accounting/starter.svg";
import arrowUp from "assets/img/icons/white/arrow-up-2.svg";
import Sidebar from "./Sidebar";
import MainDetails from "./MainDetails";
import { useAnalytics } from "hooks";

import {
  ProfileWrapper,
  MainWrapper,
  BackButtonWrapper,
  BackButtonClickable,
  BackButtonTitle,
  PartnerName,
  PartnerNameWrapper,
  SidebarWrapper,
  WebsiteLink,
  Icon,
} from "./elements";

export const addStatusIcon = (status) => {
  const tier = status.toLowerCase();
  switch (tier) {
    case "elite":
      return elite;
    case "premium":
      return premium;
    default:
      return starter;
  }
};

const addAddresses = (partner) => {
  const structuredAddresses = [];
  const keys = [
    "additionalAddress1",
    "additionalAddress2",
    "additionalAddress3",
    "additionalAddress4",
  ];

  keys.forEach((address) => {
    if (partner[address]) {
      structuredAddresses.push({
        key: address,
        value: createBody(partner[address]),
      });
    }
  });

  if (structuredAddresses.length === 0) {
    structuredAddresses.push({
      key: "hqAddress",
      value: createBody(partner.hqAddress),
    });
  }

  return structuredAddresses;
};

const addSocialIcons = (facebookPage, linkedinProfile, twitterProfile) => {
  const imageIcons = [fb, linkedin, twitter];

  return [facebookPage, linkedinProfile, twitterProfile].reduce(
    (icons, link, index) => {
      if (link) {
        icons.push({
          link,
          imageSource: imageIcons[index],
        });
      }

      return icons;
    },
    [],
  );
};

// looks through answers array for specific matching id
// and then filters for matching answers with selected partner
// and maps out display name
const getSpecificAnswers = (questions, partnerAnswers, questionId) =>
  pipe(
    find(propEq("id", questionId)),
    (question) => question.answers,
    filter((answer) => partnerAnswers.indexOf(answer.searchTerm) > -1),
    map((answer) => ({
      key: answer.searchTerm,
      value: answer.displayTerm,
    })),
  )(questions);

const PartnerProfile = ({ pageContext: { partner, questions }, location }) => {
  const {
    answers,
    clientSize,
    facebookPage,
    linkedinProfile,
    twitterProfile,
    hqAddress,
    detailedDescription,
    name,
    industriesServed,
    website,
    tier,
    tierReward = "",
  } = partner;

  const { trackEvent } = useAnalytics();

  const sidebarDescriptions = [
    {
      key: "sidebar1",
      icon: addStatusIcon(tier),
      title: <b>{`${tier.toUpperCase()} PARTNER`}</b>,
      description: `${tier.toLowerCase() === "elite" ? tierReward : ""}`,
    },
    {
      key: "sidebar2",
      icon: pin,
      title: "Headquarters",
      description: createBody(hqAddress),
    },
    {
      key: "sidebar3",
      icon: globe,
      title: (
        <WebsiteLink
          onClick={() => {
            trackEvent({
              value: "Click: Partner Outbound Link",
              properties: {
                url: website,
              },
            });
          }}
          href={website}
          target="_blank"
          rel="noopener noreferrer"
        >
          Website
        </WebsiteLink>
      ),
    },
    {
      key: "sidebar4",
      icon: team,
      title: "Client size",
      description: clientSize.map((size) => <div key={size}>{size}</div>),
    },
  ];

  const socialIcons = addSocialIcons(
    facebookPage,
    linkedinProfile,
    twitterProfile,
  );

  const processedQuestions = questions.map((question) => ({
    ...question,
    answers: question.answers.map((answer) => answer.fields),
  }));
  const processedAnswers = answers.map((answer) => answer.fields.searchTerm);
  const services = getSpecificAnswers(
    processedQuestions,
    processedAnswers,
    "serviceProvided",
  );
  const software = getSpecificAnswers(
    processedQuestions,
    processedAnswers,
    "toolUsed",
  );
  const bullets = software.map((bullet) => {
    const lowercaseBullet = bullet.value.toLowerCase();
    if (lowercaseBullet.includes("quickbooks")) {
      return {
        src: qb,
      };
    } else if (lowercaseBullet.includes("xero")) {
      return {
        src: xero,
      };
    } else if (lowercaseBullet.includes("expensify")) {
      return {
        src: expensify,
      };
    } else if (lowercaseBullet.includes("netsuite")) {
      return {
        src: netsuite,
        height: 31,
        width: 68,
      };
    } else if (lowercaseBullet.includes("concur")) {
      return {
        src: concur,
      };
    } else {
      return null;
    }
  });

  const detailedPoints = [
    {
      title: "Services offered",
      points: services,
    },
    {
      title: "Software expertise",
      points: software,
      bullets,
    },
    {
      title: "Industries served",
      points: industriesServed.map((industry) => ({
        key: industry,
        value: industry,
      })),
    },
    {
      title: "Locations",
      points: addAddresses(partner),
    },
  ];

  const handleClick = () => {
    navigate(
      `/portal/accounting?${new URLSearchParams(window.location.search)}${
        window.location.hash
      }`,
    );
  };

  return (
    <Layout
      title={`${name}, a Brex Accounting Partner | Brex`}
      location={location}
      whiteContentBackground
      autoScrollBehavior
      hideDefaultFooter
    >
      <BackButtonWrapper>
        <BackButtonClickable onClick={handleClick}>
          <Icon src={arrowUp} />
          <BackButtonTitle>BACK TO SEARCH RESULTS</BackButtonTitle>
        </BackButtonClickable>
      </BackButtonWrapper>
      <PartnerNameWrapper>
        <PartnerName>{name}</PartnerName>
      </PartnerNameWrapper>
      <ProfileWrapper>
        <SidebarWrapper>
          <Sidebar
            partner={partner}
            socialIcons={socialIcons}
            sidebarDescriptions={sidebarDescriptions}
            questions={questions}
          />
        </SidebarWrapper>
        <MainWrapper>
          <MainDetails
            detailedDescription={detailedDescription}
            detailedPoints={detailedPoints}
          />
        </MainWrapper>
      </ProfileWrapper>
    </Layout>
  );
};

export default PartnerProfile;
